import React, { Component } from "react";
import "./Header.scss";
import { GiHamburgerMenu } from "react-icons/gi";
import { RiArrowDownSLine, RiArrowUpSLine } from "react-icons/ri";
import { FaUserCircle } from "react-icons/fa";
import HeaderDropdownList from "./HeaderDropdownList";

class Header extends Component {
  state = {
    isVisible: false,
    lists: [
      {
        id: 1,
        title: "항목 1",
      },
      {
        id: 2,
        title: "항목 2",
      },
      {
        id: 3,
        title: "항목 3",
      },
    ],
  };

  onClick = () => {
    this.setState({
      isVisible: !this.state.isVisible,
    });
  };

  render() {
    const { lists: LISTS, isVisible } = this.state;
    const { onClick } = this.props;

    return (
      <header className="header">
        <button
          onClick={() => {
            onClick();
          }}
          className="header__btn header__btn--SVG"
        >
          <GiHamburgerMenu />
        </button>
        <button className="header__btn header__btn--link">
          <a href="#" className="header__link">
            LOGO
          </a>
        </button>
        <div className="header__wrap">
          <button
            onClick={() => {
              this.onClick();
            }}
            className="header__btn header__btn--dropdown"
          >
            <span className="header__text">
              (주)엔엑스 테크놀로지
              {isVisible === true ? <RiArrowUpSLine /> : <RiArrowDownSLine />}
            </span>
          </button>
          {isVisible === true && <HeaderDropdownList LISTS={LISTS} />}
        </div>
        <button className="header__btn header__btn--SVG header__btn--right">
          <FaUserCircle />
        </button>
      </header>
    );
  }
}

export default Header;
