import React, { Component } from "react";
import "./NavigationSubList.scss";

class NavigationSubList extends Component {
  render() {
    const { SUB_LISTS } = this.props;

    return (
      <ul
        onClick={(e) => {
          if (e.target.nodeName !== "SPAN") {
            return;
          }

          const SUB_LISTS = document.getElementsByClassName("sub-nav__name");

          for (const SUB_LIST of SUB_LISTS) {
            if (SUB_LIST.classList.contains("sub-nav__name--isClicked")) {
              SUB_LIST.classList.remove("sub-nav__name--isClicked");
            }
          }

          e.target.classList.add("sub-nav__name--isClicked");
        }}
        className="sub-nav__list"
      >
        {SUB_LISTS.map((subList, index) => (
          <li key={index} className="sub-nav__item">
            <button className="sub-nav__btn">
              <span className="sub-nav__name">{subList}</span>
            </button>
          </li>
        ))}
      </ul>
    );
  }
}

export default NavigationSubList;
