import React, { Component } from "react";
import "./NavigationListItem.scss";
import { RiArrowDownSLine, RiArrowUpSLine } from "react-icons/ri";
import NavigationSubList from "./NavigationSubList";

class NavigationListItem extends Component {
  render() {
    const { LIST, onClick } = this.props;

    return (
      <li className="nav__item">
        <button
          onClick={() => {
            onClick(LIST.id);
          }}
          className={
            "nav__btn" + (LIST.isClicked === true ? " nav__btn--isClicked" : "")
          }
        >
          <span className="nav__name">
            {LIST.icon}
            {LIST.title}
            {LIST.hasOwnProperty("subLists") === true && (
              <div>
                {LIST.isClicked === true ? (
                  <RiArrowUpSLine />
                ) : (
                  <RiArrowDownSLine />
                )}
              </div>
            )}
          </span>
        </button>
        {LIST.hasOwnProperty("subLists") === true &&
          LIST.isClicked === true && (
            <NavigationSubList SUB_LISTS={LIST.subLists} />
          )}
      </li>
    );
  }
}

export default NavigationListItem;
