import React, { Component } from "react";
import "./Header.scss";
import "./HeaderDropdownList.scss";

class HeaderDropdownList extends Component {
  render() {
    const { LISTS } = this.props;
    const DROPDOWN_LIST = LISTS.map((list) => (
      <li key={list.id} className="header__dropdown-item">
        <button className="header__dropdown-btn">{list.title}</button>
      </li>
    ));

    return (
      <ul className="header__dropdown">
        {/* {LISTS.map((list) => (
          <li key={list.id} className="header__dropdown-item">
            <button className="header__dropdown-btn">{list.title}</button>
          </li>
        ))} */}
        {DROPDOWN_LIST}
      </ul>
    );
  }
}

export default HeaderDropdownList;
