import React, { Component } from "react";
import "./Navigation.scss";
import NavigationList from "./NavigationList";
import { VscDashboard } from "react-icons/vsc";
import {
  AiOutlineSearch,
  AiOutlineControl,
  AiOutlineTool,
} from "react-icons/ai";
import { GiElectric, GiCircularSaw } from "react-icons/gi";
import { FiMonitor } from "react-icons/fi";
class Navigation extends Component {
  state = {
    lists: [
      {
        isClicked: true,
        id: 1,
        title: "대시보드",
        icon: <VscDashboard />,
      },
      {
        isClicked: false,
        id: 2,
        title: "검색 및 분석",
        icon: <AiOutlineSearch />,
        subLists: ["검색 및 분석", "리포트"],
      },
      {
        isClicked: false,
        id: 3,
        title: "일괄 제어",
        icon: <AiOutlineControl />,
      },
      {
        isClicked: false,
        id: 4,
        title: "관리",
        icon: <AiOutlineTool />,
        subLists: [
          "지역, 허브, 장치 관리",
          "스케줄 관리",
          "사용자 관리",
          "사용량 합산",
          "장치 일괄설정",
        ],
      },
      {
        isClicked: false,
        id: 5,
        title: "에너지절감 정책",
        icon: <GiElectric />,
        subLists: ["정책 템플렛", "지역단위 정책", "그룹단위 정책"],
      },
      {
        isClicked: false,
        id: 6,
        title: "로그 관리",
        icon: <FiMonitor />,
      },
      {
        isClicked: false,
        id: 7,
        title: "환경설정",
        icon: <GiCircularSaw />,
      },
    ],
  };

  onClick = (id) => {
    let TEMP = this.state.lists.map((list) =>
      list.id === id
        ? list.hasOwnProperty("subLists")
          ? { ...list, isClicked: !list.isClicked }
          : { ...list, isClicked: true }
        : {
            ...list,
            isClicked: false,
          }
    );

    this.setState({
      lists: TEMP,
    });
  };

  render() {
    const { lists: LISTS } = this.state;

    return (
      <nav className="nav">
        <NavigationList LISTS={LISTS} onClick={this.onClick} />
      </nav>
    );
  }
}

export default Navigation;
