import React, { Component, Fragment } from "react";
import "./App.scss";
import Header from "./components/Header";
import Navigation from "./components/Navigation";
import ContentTemplate from "./components/ContentTemplate";

class App extends Component {
  state = {
    nav: {
      isVisible: true,
    },
    main: {
      isFull: false,
    },
  };

  canvasMeter = React.createRef();

  drawMeter() {
    const canvas = this.canvasMeter.current;
    const CTX = canvas.getContext("2d");
    const { width, height } = canvas.getBoundingClientRect();
    canvas.width = width;
    canvas.height = height;
    const centerX = canvas.width / 2;
    const centerY = canvas.height / 2;
    const RADIUS = Math.min(canvas.width, canvas.height) / 2;
    CTX.strokeStyle = "transparent";
    const ANGLE = Math.PI / 6;
    const COLORS = [
      "#3ff9ba",
      "#56ffc2",
      "#6eff93",
      "#9eff86",
      "#ccfd84",
      "#fff575",
      "#fcd371",
      "#fc8375",
      "#ea5b69",
      "#f0166b",
    ];

    for (let i = 0; i < 10; i += 1) {
      CTX.beginPath();
      CTX.arc(
        centerX,
        centerY,
        RADIUS,
        ANGLE * i + (Math.PI / 180) * 100,
        ANGLE * (i + 1) + (Math.PI / 180) * 100
      );
      CTX.arc(
        centerX,
        centerY,
        RADIUS * 0.8,
        ANGLE * (i + 1) + (Math.PI / 180) * 100,
        ANGLE * i + (Math.PI / 180) * 100,
        true
      );
      CTX.fillStyle = COLORS[i];
      CTX.fill();
    }
  }

  onClick = () => {
    this.setState(
      {
        nav: {
          ...this.state.nav,
          isVisible: !this.state.nav.isVisible,
        },

        main: {
          ...this.state.main,
          isFull: !this.state.main.isFull,
        },
      },
      () => {
        this.drawMeter();
      }
    );
  };

  componentDidMount = () => {
    this.drawMeter();
  };

  render() {
    const { isVisible } = this.state.nav;
    const { isFull } = this.state.main;

    return (
      <Fragment>
        <Header onClick={this.onClick} />
        <div className="wrapper">
          {isVisible === true && <Navigation />}
          <section className={"main" + (isFull ? " main--isFull" : "")}>
            <section className="content-wrapper">
              <ContentTemplate name="실시간 소비전력">
                <canvas ref={this.canvasMeter} className="meter"></canvas>
              </ContentTemplate>
              <ContentTemplate name="누적 소비전력량"></ContentTemplate>
              <ContentTemplate name="이벤트 알림"></ContentTemplate>
            </section>
          </section>
        </div>
      </Fragment>
    );
  }
}

export default App;
